/* css for navbar  */

.navbar * {
    color: hsla(200, 100%, 50%, 0.8);
    color: var(--blue-text-clr);
    cursor: pointer;
}

.navbar ul {
    list-style: none;
    padding-left: 1rem;
}

.navbar ul.hide {
    display: none;
}

.sub-nav-group--header {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.angular-icon {
    font-weight: bolder;
    font-size: 1.25em;
    -webkit-transform: rotateZ(90deg);
            transform: rotateZ(90deg);
    transition: -webkit-transform linear 250ms;
    transition: transform linear 250ms;
    transition: transform linear 250ms, -webkit-transform linear 250ms;
    cursor: pointer;
    padding: 2px;
}

.angular-icon.rotate {
    -webkit-transform: none;
            transform: none;
}

.sub-nav-group--nav-items {
    -webkit-transform: none;
            transform: none;
    transition: -webkit-transform linear 250ms;
    transition: transform linear 250ms;
    transition: transform linear 250ms, -webkit-transform linear 250ms;
    -webkit-transform: scaleY(1);
            transform: scaleY(1)
}

.sub-nav-group--nav-items.hide {
    /* display: none; */
    -webkit-transform: scaleY(0);
            transform: scaleY(0)
}

.navbar a:hover {
    color: pink;
    color: var(--hover-clr);
}

.navbar .b1 {
    font-size: 1.3em;
    letter-spacing: 2px;
    font-weight: 700px;
}

.navbar .b2 {
    border-bottom: 0.5px solid hsla(0, 100%, 0%, 0.15);
}
