.heading {
    /* background-image: linear-gradient(to right, hsl(100, 100%, 50%), hsl(200, 100%, 50%), hsl(100, 100%, 50)); */
    background-image: linear-gradient(to right, #55ff00, #00aaff, #55ff00);
    font-size: 2rem;
    text-align: center;
    padding: 1rem;
    text-align: center;
}

.cryption-wrapper {
    padding-top: 2rem;
    background-color: thistle;
    background-image: linear-gradient(150deg, #1d75a6, #774fac, #7386c2);
    /* #00bfe6,#ffa8f3,#04B3A1 */
}


/* cryption main container */

.cryption-main-container code:empty {
    padding: 0;
}

.cryption-main-container code {
    padding: .2em .4em;
    margin: 0;
    font-size: 85%;
    background-color: rgba(98, 107, 112, 0.5);
    border-radius: 3px;
    margin-left: 1rem
}

.cryption-main-container {
    --margin: 6rem;
    margin: 02rem var(--margin) 0;
}

.cryption-main-container input {
    font-size: 1.25rem;
    padding: 6px 16px;
}

.cryption-main-container input:focus {
    --hover-clr: pink;
    border: 2px solid rgba(34, 34, 190, 0.884);
    border-radius: 7px;
    outline: none;
    background-color: var(--hover-clr);
}

.cryption-main-container input[type="text"]:focus~label {
    color: pink;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}


/* cryption container */

.cryption-container {
    margin-top: 4rem;
    display: flex;
    justify-content: space-between;
    padding-bottom: 1rem;
}

.encryption,
.decryption {
    flex: 1 1;
}


/* key encryption  */

.cryption__container {
    display: flex;
    flex-direction: column;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
    max-width: 550px;
    word-break: break-all;
    padding-left: 1rem;
}

.cryption__container>*+* {
    margin-top: 0.75rem;
}

.main-heading {
    display: flex;
    position: relative;
    align-items: center;
    align-self: stretch;
}

.main-heading::before {
    margin-right: 1rem;
}

.main-heading::after {
    margin-left: 1rem;
}

.main-heading::before,
.main-heading::after {
    content: "";
    display: block;
    padding: auto;
    height: 0.7em;
    flex: 1 1;
    border: 3px solid blue;
    border-width: 3px 0;
    width: 100%;
    box-sizing: border-box;
}


/*  key generation  */

.show-off-image {
    max-width: 550px;
    box-shadow: 0 7px 10px rgb(0 0 0 / 0.2);
}

.generation-wrapper {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    justify-content: space-between
}

.generation {
    max-width: 550px;
    flex: 2 1;
    display: flex;
    flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    align-items: center;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.generation__form {
    display: flex;
    grid-gap: 1rem;
    gap: 1rem;
    flex-direction: column;
    /* flex-direction: column; */
}

.generation__input-container {
    display: flex;
    flex-direction: column;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    flex: 1 1;
}

.generation__info {
    text-align: center;
    padding: 4px 0;
    background: radial-gradient(100% 100% at 50% 50%, rgb(245, 245, 245) 10%, #1d75a6);
    align-self: stretch;
}

.generation__keys span {
    background-color: rgba(98, 107, 112, 0.5);
    display: inline-block;
    padding: 2px 8px;
    border-radius: 3px;
}

.generation__keys {
    display: flex;
    grid-gap: 2rem;
    gap: 2rem;
}

.generation__info.error {
    color: red;
    /* border-radius: 5px; */
}


/* decryption */

.decryption__error {
    text-align: center;
    border: 2px solid rgb(179, 38, 38);
    color: rgb(190, 42, 42);
    background-color: rgba(223, 222, 222, 0.877);
}

.decryption__decrypted-text code:empty {
    padding: 0;
}


/* media query */

@media screen and (max-width:1100px) {
    .generation-wrapper {
        justify-content: center;
    }
    .cryption-main-container {
        --margin: 3rem;
    }
    .main-heading {
        align-self: stretch;
    }
    .cryption__container {
        max-width: 350px;
    }
    .show-off-image {
        display: none;
    }
}

@media screen and (max-width:800px) {
    .cryption-main-container {
        --margin: 1rem;
    }
    .generation>* {
        padding-left: 1rem;
    }
    .generation {
        width: auto;
        /* padding-left: 2rem; */
        justify-content: start;
        align-items: start;
    }
    .generation__keys {
        flex-direction: column;
    }
    .encryption {
        margin-bottom: 1rem;
    }
    .encryption>*,
    .decryption>* {
        padding-left: 1rem;
    }
    .cryption__container {
        max-width: calc(100vw - 2rem);
        align-items: flex-start;
    }
    .main-heading {
        padding-left: 0;
        align-self: stretch;
    }
    .cryption-container {
        flex-direction: column;
        display: block;
    }
    .main-heading::before,
    .main-heading::after {
        height: 0.8em;
    }
}
.hero {
    --x-dimension: 530px;
    --y-dimension: 100px;
    --x-position: 45%;
    --y-position: 30%;
    position: relative;
    height: 100vh;
    padding: 4rem 6rem 2rem 6rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
