    padding: 0.5rem 1rem;
    border-radius: 10px;
    border: 2px solid none;
    font-size: 1.75rem;
    cursor: pointer;
    background: linear-gradient(to right, rgb(68, 68, 160), rgb(180, 42, 173));
    ;
