}


/* hover  */

.hero__details-icon-container:hover .hero__tooltip {
    width: auto;
    height: auto;
    clip: auto;
    clip: initial;
    opacity: 1;
    visibility: visible;
}

.hero__tooltip {
    position: absolute;
    font-size: 16px;
    width: 1px;
    height: 1px;
    padding: 0;
    clip: rect(0, 0, 0, 0);
    white-space: nowrap;
    border: 0;
    background: linear-gradient(to right, rgb(68, 68, 160), rgb(180, 42, 173));
    color: white;
    line-height: normal;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    left: 90%;
    padding: 0.5rem 1rem;
    border-radius: 4px;
    display: inline-block;
    opacity: 0;
    visibility: hidden;
    transition: 0.2s;
}

.hero__tooltip::before {
    content: "";
    right: 100%;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    position: absolute;
    z-index: 1;
    width: 0;
    height: 0;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
    border-left: 5px solid transparent;
    border-right: 5px solid black;
}

