.card {
    padding: 1rem;
    border: 2px solid transparent;
    border-radius: 5px;
    --opacity: 0.7;
    --color-1: rgba(255, 132, 255, var(--opacity));
    --color-2: hsla(204, 100%, 88%, var(--opacity));
    display: flex;
    flex-direction: column;
    align-items: center;
    background-image: radial-gradient(farthest-corner at 20% 20%, hsla(262, 100%, 25%, 0.75) 25px, hsla(148, 100%, 15%, 0.75));
    color: white;
    /* transition: transform linear 100ms; */
}

.card.card-hover::before {
    background-color: rgba(0, 0, 0, 0.3);
    content: '';
    display: block;
    position: absolute;
    inset: 0;
}

.card:nth-child(2n) {
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
}

.card:nth-child(2n-1) {
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
}

.card:nth-child(n):hover {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
}

.card__profile-image-container {
    display: inline-block;
    border-radius: 50%;
    position: relative;
}

.card__profile-image {
    position: relative;
    display: block;
    --dimension: 170px;
    border: 1px solid transparent;
    border-radius: 50%;
    width: var(--dimension);
    height: var(--dimension);
    z-index: 1;
    z-index: 1;
}

.card__profile-image-container::before {
    --opacity: 0.25;
    --color-1: rgba(0, 132, 255, var(--opacity));
    --color-2: hsla(300, 100%, 58%, var(--opacity));
    background: linear-gradient(to bottom, var(--color-1), var(--color-2));
    content: '';
    display: block;
    position: absolute;
    border-radius: 50%;
    inset: 0;
    z-index: 3;
}

.card__name {
    letter-spacing: 2px;
}

.card__media-icon {
    --dimension: 23px;
    width: var(--dimension);
    height: var(--dimension);
    transition: -webkit-transform linear 100ms;
    transition: transform linear 100ms;
    transition: transform linear 100ms, -webkit-transform linear 100ms;
}

.card__links>a:not(:first-child) {
    margin-left: 1rem;
}

.card__media-icon:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.card__links>a {
    position: relative;
    z-index: 100;
}

